@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-600: #0891b2;
  --primary-700: #0369a1;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
} 
.exteriorbanner{
  background-image: url(../public/exterior-texture-banner.webp);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 80vh;
}


.second-band {
  padding: 70px 50px;
  position: relative;
  overflow: hidden;
}

.second-band .content {
  text-align: center;
  margin-bottom: 55px;
}

.second-band .content h1 {
  color: #000;
  font-size: 40px;
  font-weight: 500;
}

.second-band .content p {
  font-size: 17px;
  font-weight: 400;
}

.background-image img {
  width: 100%;
  height: 280px;
  border-radius: 30px;
  transition: transform 0.5s ease-in-out;
}

.second-band-box {
  margin-bottom: 30px;
  position: relative;
  transition: 0.5s;
  overflow: hidden;
  border-radius: 30px;
}

.second-box-content {
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 1;
  z-index: 1;
  background-color: #f7f2e7;
  color: #000;
  padding: 10px;
  border-radius: 30px;
  padding-bottom: 0;
  transition: 0.5s;
  transform: translate(-50%, -50%); /* Ensure content is centered */
}

.overlay-content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  color: #fff;
  padding: 54px;
  opacity: 0; /* Start with hidden */
  visibility: hidden; /* Ensure it's not interactable */
  transition: opacity 0.5s ease, visibility 0s 0.5s; /* Smooth opacity change and delay visibility */
  z-index: 2; /* Ensure overlay content appears above the overlay */
  transform: translate(-50%, -50%); /* Ensure content is centered */
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 30px;
  background-color: rgba(0, 0, 0, 0.5); /* Transparent black background */
  z-index: 1; /* Ensure the overlay stays above the image */
  opacity: 1; /* Start with no visibility */
  transition: opacity 0.5s ease; /* Smooth transition */
}

.second-band-box:hover .second-box-content {
  display: none;
}

.second-band-box:hover .overlay-content {
  opacity: 1; /* Fade in the overlay content */
  visibility: visible; /* Make the content visible */
  transition: opacity 0.5s ease, visibility 0s 0s; /* Ensure visibility is immediately available after the fade */
}
.second-band-box button {
  padding: 10px 20px;
  border-radius: 30px !important;
  background: #fec940;
  font-weight: 500;
  cursor: pointer;
  color: #000;
  transition: 0.5s;
  border: 2px solid #fec940;
  width: 150px;
  transition: 0.5s;
  font-size: 15px;
  display: flex;
    align-items: center;
}
.second-band-box button .icon{
  transition: 0.5s;
  margin-left: 5px;
}
.second-band-box button:hover .icon{
  transform: translateX(5px);
}
.second-band-box button:hover{
  box-shadow: 0px 10px 8px #00000085;
}
.overlay-content p {
  font-size: 17px;
  font-weight: 400;
  margin-bottom: 20px;
}
.overlay-content h5 {
  font-size: 25px;
  font-weight: 500;
}

.second-band .second-band-box:hover img {
  transform: translate(5px, 5px) scale(1.1); 
  transition: transform 0.5s ease-in-out;
  border-radius: 30px;
}